/*
Variables

Weight: 1

Styleguide: base.vars
 */


/*
Widths

@widthS - 600px;
@widthM - 800px;
@widthL - 1050px;

Styleguide: base.vars.width
 */

@widthS : 500px;
@widthM : 700px;
@widthL : 960px;


/*
Media queries

@mobile      - ~'only screen and (max-width : 767px)'
@tablet      - ~'only screen and (min-width : 768px) and (max-width : 991px)'
@tabletUp    - ~'only screen and (min-width : 768px)'
@tabletDown  - ~'only screen and (max-width : 991px)'
@desktop     - ~'only screen and (min-width : 992px) and (max-width : 1199px)'
@desktopUp   - ~'only screen and (min-width : 992px)'
@desktopDown - ~'only screen and (max-width : 1199px)'
@desktopBig  - ~'only screen and (min-width : 1200px)'

Styleguide: base.vars.media
 */

@mobile      : ~'only screen and (max-width : 767px)';
@tablet      : ~'only screen and (min-width : 768px) and (max-width : 991px)';
@tabletUp    : ~'only screen and (min-width : 768px)';
@tabletDown  : ~'only screen and (max-width : 991px)';
@desktop     : ~'only screen and (min-width : 992px) and (max-width : 1199px)';
@desktopUp   : ~'only screen and (min-width : 992px)';
@desktopDown : ~'only screen and (max-width : 1199px)';
@desktopBig  : ~'only screen and (min-width : 1200px)';


/*
Swatches

Markup:
<div class="lay lay--fixed lay--center lay--s style_layout-example">
	<div style="background-color: #f2f2f2">@swatchBody</div>
	<div style="background-color: #666666">@swatchFont</div>
	<div style="background-color: #ff6666">@swatchRed</div>
	<div style="background-color: #6666ff">@swatchBlue</div>
</div>

Styleguide less.vars.swatch
 */

@swatchBody       : #FFC116;
@swatchFont       : #555;
@swatchGray       : #222222;
@swatchYellow     : #FFC116;
@swatchRed        : #ff6666;
@swatchBlue       : #6666ff;
@swatchPurple     : #A500FF;
@swatchBlack      : #030205;
@swatchStyleGuide : @swatchRed;


/*
Misc

@fontFamily - 'Open Sans', Helvetica, Arial, serif

Styleguide less.vars.misc
 */

@fontFamily : Helvetica,'Open Sans', Arial, serif;
